.forecast-element-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 2px dashed rgb(131, 131, 131);
}

.forecast-element-header__left-child {
    font-size: 16px;
    width: 45%;
    display: flex;
    justify-content: space-between;
}

.forecast-element-header__right-child {
    display: flex;
    align-items: center;
    font-size: 16px;
}

.icon-small {
    width: 61px;
    height: 45px;
}