.current-weather {
    padding: 10%;
    height: 40vh;
    background: 
        no-repeat center/50% url(../../../public/pictures/clear_day_small.png),
        linear-gradient(rgb(255, 55, 0), rgb(255,173,0), rgb(255,173,0, 0));
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
}

.current-weather-header {
    width: 100%;
    margin: 20px 0px 40px 0px;
    color: rgb(245, 245, 245);
}

.current-weather-footer {
    padding: 0;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.current-weather-footer__conditions {
    font-size: 24px;
    margin: 0;
    padding: 0;

}

.current-weather-footer__temp {
    margin: 0;
    padding: 0;
    font-size: 72px;
}